html,body{height:100%}html,body,input,textarea,buttons{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-shadow:1px 1px 1px rgba(0,0,0,0.004)}#header{position:fixed;top:0;right:0;left:0;z-index:2;-webkit-transition:left .2s;transition:left .2s}#sidedrawer{position:fixed;top:0;bottom:0;width:200px;left:-200px;overflow:auto;z-index:2;background-color:#fff;-webkit-transition:-webkit-transform .2s;transition:-webkit-transform .2s;transition:transform .2s;transition:transform .2s, -webkit-transform .2s;box-shadow:1px 0 0 rgba(0,0,0,0.1)}#content-wrapper{min-height:100%;overflow-x:hidden;margin-left:0;-webkit-transition:margin-left .2s;transition:margin-left .2s;margin-bottom:-64px;padding-bottom:64px}#footer{height:64px;margin-left:0;-webkit-transition:margin-left .2s;transition:margin-left .2s}@media (min-width:768px){#header{left:200px}#sidedrawer{-webkit-transform:translate(200px);transform:translate(200px)}#content-wrapper{margin-left:200px}#footer{margin-left:200px}body.hide-sidedrawer #sidedrawer{-webkit-transform:translate(0);transform:translate(0)}body.hide-sidedrawer #header{left:0}body.hide-sidedrawer #content-wrapper{margin-left:0}body.hide-sidedrawer #footer{margin-left:0}}.sidedrawer-toggle{color:#fff;cursor:pointer;font-size:20px;line-height:20px;margin-right:10px}.sidedrawer-toggle:hover{color:#fff;text-decoration:none}#sidedrawer-brand{padding-left:20px;font-size:20px;font-weight:400}#sidedrawer.active{-webkit-transform:translate(200px);transform:translate(200px)}#sidedrawer ul{list-style:none}#sidedrawer>ul{padding-left:0}#sidedrawer>ul li>ul{display:none}#sidedrawer>ul>li:first-child{padding-top:15px}#sidedrawer strong{display:block;padding:15px 22px;cursor:pointer}#sidedrawer strong:hover{background-color:#E0E0E0}#sidedrawer strong+ul>li{padding:6px 0}#footer{background-color:#0288D1;color:#fff}#footer a{color:#fff;text-decoration:underline}#footer .copy-right{line-height:64px;margin:0}


